import React from 'react';
import s from './yourprofile.module.scss'
import {EditInput, EditTextarea} from "../../../common/editinput";
import AvatarContainer from "../avatar/avatarContainer";

const YourProfile = (props) =>{

        return(
                <div className="container">
                    <div className={s.profile_cont}>
                        <div className={s.photo_col}>
                            <AvatarContainer />
                        </div>
                        <div className={s.info_col}>
                            <div className={s.info_inputs}>
                                <EditInput inputText={props.name} putInfo={props.putProfileInfo} name={"name"} placeholder={"  "}/>
                                <EditInput inputText={props.soName} putInfo={props.putProfileInfo} name={"soName"} placeholder={"  "}/>
                                <EditInput inputText={props.company} putInfo={props.putProfileInfo} name={"company"} placeholder={"  "}/>
                                <EditTextarea inputText={props.description} putInfo={props.putProfileInfo} name={"description"} placeholder={" "} />
                            </div>
                        </div>

                    </div>
                </div>
        )

}




export default YourProfile;